import Layout from '../../components/basicLayout';
import { Component } from 'react'
import axios from 'axios';

class BlogForm extends Component {

  constructor(props) {
  	super(props);
  	this.blog = {
  		title: null,
  		content: null
  	};
  }

  submitForm(event) {
    event.preventDefault();

  }

  render() {
    return (
    	<Layout>
    		<h1>Create Blog</h1>
	      <form onSubmit={this.submitForm}>
	        <label>
	          Title:
	          <input type="text" value={this.blog.title} />
	        </label>
	        <input type="submit" value="Submit" />
	        <label>
	          Content:
	          <input type="text" value={this.blog.content} />
	        </label>
	        <input type="submit" value="Submit" />
	      </form>
      </Layout>
    );
  }
}

export default BlogForm;